<template>
  <div>
    <div v-if="item" style="margin-bottom:48px;" class="event-final">
      <p v-on:click="goBack">back</p>
      <div style="margin:2px;">
        <div style="width:100%; margin-top:48px; text-align:center;">
          <img :src=item.logo.url style="width:100%; max-width:480px;"/>
        </div>
        <h3 v-html=item.name.html></h3>
        <br/>
        <span v-html=item.description.html></span>
        <button @click="openlink()">MORE INFORMATION</button>
      </div>
      <p v-on:click="goBack">back</p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'event',
  data () {
    return {
      item: null,
      id: null
    }
  },
  mounted() {
    console.log('here')
    this.id = this.$route.params.id
    try{
      this.item = this.getEventById(this.id)
    } catch (e) {
      this.$router.push('/events')
    }
  },
  methods: {
    openlink: function (target) {
      window.open(this.item.url, '_blank')
    },
    goBack() {
      this.$router.go(-1)
    }
  },
  computed: {
    ...mapGetters([
      'getEventById'
    ])
  }
}
</script>

<style>
.event-final {
  padding: 16px 8px;
  background-color: rgba(0,0,0,.87);
  width:100%;
}
</style>



