<template>
  <div class="item-out">
    <img v-bind:src="item.logo.url" v-on:click="open(item.id)">    
  </div>
</template>

<script>
export default {
  name: 'event-item',
  props: [
    'item'
  ],
  mounted () {
  },
  methods: {
    open (id) {
      this.$router.push(`/event/${id}`)
    }
  }
}
</script>

<style>
.item-out {
  width: 100%;;
  text-align: center;
  margin:16px 0px;
  color: #fff;
  padding: 8px;
}

.event-overlay {
  position: absolute;
  z-index: 333;
  background-color: rgba(0,0,0,.85);
  width: 100%;
  text-align: left;
  padding: 12px;
  color: #fff;
}

.item-out img {
  width: 95%;
}
h4 { color: #fff; }
</style>


