import { storage } from '../../utils/storage'

const state = {
  allData: null,
  eventData: null,
  lastUpdated: null
}

const mutations = {
  setData (state, data) {
    state.eventData = data
  }, 
  setLastUpdated (state, data) {
    state.lastUpdated = data
  }
}

const getters = {
  lastUpdated (state) {
    let lu = storage.getValue('lastUpdated');
    if(lu.toString().length > 0){
      return lu
    } else {
      return state.lastUpdated
    }
  },

  eventData (state) {
   
      return storage.getValue('events')
  
  },

  getEventById: (state) => (id) => {
    let item, t
    for (let i = 0; i < state.eventData.data.length; i++) {
      t = state.eventData.data[i]
      if (t.id === id) {
        item = t
      }
    }
    return item
  }

}

const actions = {
  setData (context, data) {
    storage.storeValue('events', data)
    context.commit('setData', {state, data})
  },
  setLastUpdated (context, data) {
    storage.storeValue('lastUpdated', data)
    context.commit('setLastUpdated', data)
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



